.timeline .timeline-list .timeline-item {
  padding: 20px;
  color: white;
  background: transparent;
  border-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.9);
  border-radius: 10px;
  margin-bottom: 20px !important;
}

.timeline .timeline-list .timeline-item:last-child {
  margin-bottom: 0px;
}

.timeline-event {
  font: normal normal normal 25px/30px Poppins SemiBold;
  text-shadow: 0 0 15px rgb(5, 0, 0);
  margin-bottom: 10px;
}

.timeline-detail {
  font: normal normal normal 17px/30px Poppins Regular;
  text-shadow: 0 0 15px rgb(5, 0, 0);
}

.timeline-hover-prompt {
  font: normal normal normal 17px/30px Poppins Regular;
  text-shadow: 0 0 15px rgb(5, 0, 0);
}

.timeline-content .timeline-date {
  font: normal normal normal 13px/30px Poppins Light;
  text-shadow: 0 0 15px rgb(5, 0, 0);
  margin-bottom: 10px;
  letter-spacing: 2px;
}

