  .timeline::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 2px;
    left: 50%;
    transform: translateX(-50%);
    background-color: gray;
  }

  .timeline .timeline-list .timeline-item {
    width: 50%;
    margin-bottom: 50px !important;
    position: relative;
  }

  .timeline .timeline-list .timeline-item:nth-child(odd) {
    float: left;
    clear: right;
    transform: translateX(-30px);
    border-radius: 20px 0px 20px 20px;
  }

  .timeline .timeline-list .timeline-item:nth-child(even) {
    float: right;
    clear: left;
    transform: translateX(30px);
    border-radius: 0px 20px 20px 20px;
  }

  .timeline .timeline-list .timeline-item::after {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    background-color: gray;
    border-radius: 50%;
    top: 0;
  }

  .timeline .timeline-list .timeline-item:nth-child(odd)::after {
    transform: translate(50%, -50%);
    right: -30px;
  }

  .timeline .timeline-list .timeline-item:nth-child(even)::after {
    transform: translate(-50%, -50%);
    left: -30px;
  }

  .timeline .timeline-list .timeline-item:hover:after {
    transition: all 0.2s ease-in;
    background-color: white;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  }

  .timeline-item:hover {
    transition: all 0.2s ease-in;
    background-color: rgba(255, 255, 255, 0.08) !important;
    box-shadow: 0 0 50px rgba(255, 255, 255, 0.2);
  }

  .timeline-content .timeline-date {
    position: absolute;
    top: -28px;
  }
}
